"use client";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { createElement, type ComponentType } from "react";
import {
  HouseSimpleIcon,
  ShoppingBagIcon,
  DressIcon,
  SealPercentIcon,
  UserIcon,
  GearIcon,
  SignOutIcon,
} from "@phosphor-icons/react";

const navMenu: { name: string; link: string; icon: ComponentType<any> }[] = [
  {
    name: "Overview",
    link: "/admin-dashboard/overview",
    icon: HouseSimpleIcon,
  },
  {
    name: "Order Management",
    link: "/admin-dashboard/order-management",
    icon: ShoppingBagIcon,
  },
  { name: "Inventory", link: "/admin-dashboard/inventory", icon: DressIcon },
  {
    name: "Discounts",
    link: "/admin-dashboard/discounts",
    icon: SealPercentIcon,
  },
  { name: "Customers", link: "/admin-dashboard/customers", icon: UserIcon },
  { name: "Settings", link: "/admin-dashboard/settings", icon: GearIcon },
];

export default function Sidebar() {
  const pathname = usePathname();

  const iconClass = "w-5 h-5";

  return (
    <div className="w-[12.375rem] h-screen hidden lg:block fixed left-0 top-0 border-r-2 border-neutral-100 bg-white">
      <div className="w-full h-[90%] flex flex-col justify-between">
        <Link href="/" className="block w-full mb-7">
          <Image
            src="/logo/logo.png"
            alt="Araafit logo"
            width={90}
            height={36}
            className="w-[5.626rem] h-auto"
            priority
          />
        </Link>

        <div className="flex flex-col gap-4">
          {navMenu.map((item, idx) => {
            const isActive = pathname === item.link;

            return (
              <Link
                key={idx}
                href={item.link}
                className={`px-3 py-3 transition w-full flex items-center justify-between
                    ${
                      isActive
                        ? "bg-primary-900 text-white"
                        : "text-gray-700 hover:bg-primary-900 hover:text-white"
                    }`}
              >
                <div className="flex items-center text-base">
                  {createElement(item.icon ?? "a", { className: iconClass })}
                  <span className="capitalize text-sm ml-2">{item.name}</span>
                </div>
              </Link>
            );
          })}
        </div>
      </div>

      <div className="text-[0.875rem] flex items-center gap-3 text-neutral-900 border-t-2 border-neutral-100 p-[0.5rem] hover:bg-primary-900 hover:text-white cursor-pointer">
        <SignOutIcon />
        <span>Logout</span>
      </div>
    </div>
    // </div>
  );
}
